<template>
	<v-app-bar app elevation="3" color="white">
    <v-app-bar-nav-icon 
      class="d-flex d-lg-none mr-3"
      @click.stop="sidebar"
    ></v-app-bar-nav-icon>
    <v-chip 
      label 
      class="grey lighten-4 purple--text">
      {{ title[$route.name] }}
    </v-chip>
    <v-spacer></v-spacer>
    <v-col 
      cols="12" sm="4" 
      class="pa-0">
      <div class="d-flex">
        <v-autocomplete
          v-if="$route.name === 'progress-tenant' || $route.name === 'show-response'"
          placeholder="Pilih startup"
          :items="dataStartup"
          item-text="name"
          item-value="id"
          v-model="selectedStartup"
          dense
          outlined flat
          class="mt-6"
        ></v-autocomplete>
        <v-autocomplete
          v-if="$route.name === 'show-response'"
          :disabled="selectedStartup===''?true:false"
          placeholder="Pilih traction"          
          :items="dataTraction"
          item-text="judul"
          item-value="id"
          v-model="selectedTraction"
          dense
          outlined flat
          class="mt-6 ml-2"
        ></v-autocomplete>
      </div>
    </v-col>
    <v-spacer></v-spacer>
    <v-col 
      cols="12" sm="4" 
      class="pa-0">
      <v-autocomplete
        placeholder="search for people, places, and things"
        dense
        solo flat
        prepend-inner-icon="mdi-magnify"
        class="mt-6 search"
      ></v-autocomplete>
    </v-col>
  </v-app-bar>
</template>

<style>
  .search.theme--light.v-text-field--solo > .v-input__control > .v-input__slot {
    background: #f8f8f8;
  }
</style>

<script>
  import { mapState } from 'vuex'

  export default{
    props: ['permanent'],

    data: () => ({
      title: {
        'incubator': 'Dashboard',
        'profil': 'Profil Incubator',
        'edit-profil': 'Edit Profil',
        'traction': 'Daftar Traction',
        'create-traction': 'Create Traction',
        'tenant': 'Daftar Tenant',
        'progress-tenant': 'Progress Tenant',
        'daftar-sprint': 'Daftar Sprint',
        'startup-traction': 'Daftar Respon',
        'edit-traction': 'Edit Traction',
        'show-response': 'Lihat Respon'
      },
      startup: ['Smartedu'],
      selectedStartup: '',
      selectedTraction: ''
    }),

    computed: {
      ...mapState('dataIncubatorTraction', ['dataTraction']),
      ...mapState('dataIncubatorStartup', ['dataStartup']),
    },

    watch: {
      selectedStartup() {
        this.selectedTraction = ''
      },
      selectedTraction(val) {
        if (val !== '') {
          this.$store.dispatch('dataIncubatorTraction/getDataTractionResponse', {
            traction_id: val,
            startup_id: this.selectedStartup
          })
          this.$store.dispatch('dataIncubatorTraction/updateIsLoading', true)
        }
      }
    },

    methods: {
      sidebar() {
        this.$emit('editPermanent', !this.permanent)
      }
    },
  }
</script>
