    </v-footer>
	</v-app>
</template>

<script>
import Sidebar from '@/components/Incubator/Sidebar'
import Appbar from '@/components/Incubator/Appbar'

export default {
  components: {
		Appbar,
		Sidebar
  },

  data: () => ({
		permanent: false
  }),

  methods: {
		editPermanent(val) {
			this.permanent = val
		}
